import './button.css';

export const createJson = ({
  json = ""
}) => {
  
  var result = "";
  for (var i = 0; i < json.length; i++) {
    
    
    if(json[i].question != undefined)
    {
      result += "<h3>"+json[i].question+"</h3>" +
        "<p>"+json[i].answer+"</p>";
    }
    else
    {
      result += "<table>";

      for (var x = 0; x < json[i].length; x++) 
      {
        result+="<tr>";
        for (var y = 0; y < json[i][x].length; y++) 
        {
          result+="<td>"+json[i][x][y]+"</td>";
        }
        result+="</tr>";
      }
      result+="</table>";



      console.log("Frank:" + json[i]);
    }



    // more statements
 }
 


  return result;
};
